<template>
    <div class="yu-hesperis">
        <el-container style="height: 100vh">
            <el-header>Header</el-header>
            <el-container style="height: 100%">
                <el-aside width="200px">
                    <el-row style="height: 100%">
                        <el-col  style="height: 100%">
                            <Menu @select="onSelectHandler"></Menu>
                        </el-col>
                    </el-row>
                </el-aside>
                <el-container>
                    <el-main>
                        <el-card shadow="never">
                            <el-breadcrumb separator="/">
                                <el-breadcrumb-item v-for="(item,i) in Breadcrumbs" :key="i" :to="item.to">{{item.title}}</el-breadcrumb-item>
                            </el-breadcrumb>
                        </el-card>
                        <br>
                        <el-row>
                            <router-view></router-view>
                        </el-row>
                    </el-main>
                </el-container>
            </el-container>
        </el-container>
    </div>
</template>

<script>

    import Menu from './Menu/index'

    export default {
        name: "index",
        components:{
            Menu
        },
        data() {
            return {
                Breadcrumbs: [{
                    "to":{ path: '/admin' },
                    "title":"首页"
                }]
            }
        },
        methods: {
            onSelectHandler(data){
                let {Menus,indexPath} = data
                let breadcrumbs = []
                if(Menus && Menus.length > 0){
                    for(let item of Menus){
                        if(indexPath[0] == item.to){
                            breadcrumbs.push({
                                "to":{ path: item.to },
                                "title": item.title
                            })
                            if(item.child && item.child.length > 0 && indexPath.length == 2){
                                for(let v of item.child){
                                    if(indexPath[1] == v.to){
                                        breadcrumbs.push({
                                            "to":{ path: v.to },
                                            "title": v.title
                                        })
                                        break
                                    }
                                }
                                break
                            }
                        }
                    }
                }
                this.Breadcrumbs = breadcrumbs
            }
        }
    }
</script>

<style scoped>
    .el-header, .el-footer {
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 60px;
    }
    .el-aside {
        background-color: #D3DCE6;
        color: #333;
        line-height: 200px;
        height: 100%;
    }
    .el-aside{
        height: 100%;
    }
    .el-main {
        background-color: #E9EEF3;
        color: #333;
    }
</style>