<template>
    <el-menu
            unique-opened
            style="height: 100%"
            default-active="/admin"
            :router="true"
            @select="onSelectHandler">
        <template  v-for="(item,i) in Menus">
            <el-menu-item v-if="!item.child" :index="item.to">
                <i :class="item.icon"></i>
                <span slot="title">{{item.title}}</span>
            </el-menu-item>
            <el-submenu v-else :index="item.to">
                <template slot="title">
                    <i :class="item.icon"></i>
                    <span slot="title">{{item.title}}</span>
                </template>
                <el-menu-item v-for="(v,j) in item.child" :key="v" :index="v.to">
                    <i :class="v.icon"></i>
                    <span slot="title">{{v.title}}</span>
                </el-menu-item>
            </el-submenu>
        </template>
    </el-menu>
</template>

<script>

    import {Menus} from '../../../config/menu'

    export default {
        name: "Menu",
        data() {
            return {
                Menus: Menus
            }
        },
        methods:{
            onSelectHandler(index, indexPath){
                this.$emit('select',{Menus,indexPath})
            }
        }
    }
</script>

